.Upload {
	padding: 0;
	margin-bottom: 10px;
}

.Upload #previewUpload,
#imgUpload {
	display: none;
}

.Upload .public {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	gap: 0px;
	position: absolute;
	top: 10px;
	left: calc(50% - 250px / 2);
	height: 40px;
	width: 250px;
	background-color: #2f4f4f;
	border-radius: 8px;
	padding: 0 35px;
	box-shadow: 0 0 0 2px #171722;
	cursor: pointer;
	overflow: hidden;
	color: #d3d3d3;
	font-size: 16px;
	cursor: pointer;
	z-index: 7;
}
.Upload .public * {
	margin-left: -10px;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.Upload .public > svg {
	height: 50%;
	aspect-ratio: 1/1;
}

.Upload main {
	display: flex;
	flex-direction: column;
	position: relative;
	height: 100%;
	margin-top: 40px;
	overflow-x: hidden;
	overflow-y: auto;
}

.Upload .preview {
	display: flex;
	position: relative;
	height: 110px;
	background-color: #555555;
	border: none;
	cursor: pointer;
	margin: 0 10px;
	border-radius: 4px;
	overflow: auto;
	scrollbar-width: none;
}
.Upload .preview::-webkit-scrollbar {
	display: none;
}

.Upload .preview img {
	max-width: 100%;
	object-fit: contain;
	position: absolute;
	cursor: grab;
}
.Upload .preview #previewDrop {
	opacity: 0;
	height: 100%;
	position: absolute;
	z-index: 2;
	width: 100%;
}
.Upload .preview > svg {
	margin: auto;
	height: 30px;
}

.Upload .content {
	padding: 10px 30px;
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.Upload .editable[data-placeholder]:empty:before {
	content: attr(data-placeholder);
	color: #555555;
}
.Upload .editable {
	outline: none;
}
.Upload .editable:focus {
	outline: none;
	box-shadow: none;
}
.Upload .content .editable {
	display: block;
	width: 100%;
	background-color: #24242d;
	border-radius: 8px;
	border: none;
	padding: 8px;
	color: #d3d3d3;
}
.Upload .content .editable::-webkit-resizer {
	display: none;
}

.Upload .content #title {
	font-size: 40px;
	font-weight: 500;
	/* min-height: 57px; */
	line-height: 1em;
	resize: vertical;
}
.Upload .content #description {
	font-size: 24px;
	font-weight: 300;
	/* min-height: 190px; */
	resize: vertical;
}

.Upload #addImage {
	display: flex;
	position: fixed;
	aspect-ratio: 1/1;
	height: 60px;
	border-radius: 50%;
	background-color: #2f4f4f;
	bottom: 20px;
	right: 20px;
	overflow: hidden;
}
.Upload #addImage > svg {
	color: #d3d3d3;
	aspect-ratio: 1/1;
	margin: auto;
	height: 30px;
}
